import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// export default new Vuex.Store({
//     state: {

//     }
// })


export default new Vuex.Store({
    state: {
        isopen: false
            //用來儲存各種資料狀態用的
    },
    actions: {
        // 負責各類型的行為，非同步的指令也都要在這裡執行
    },
    mutations: {
        open(state) {
            state.isopen = true;

        },
        close(state) {
            state.isopen = false;
        }
        //只負責改變資料狀態
    },
});