import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import test from "../components/modal.vue";
import about from '../views/About.vue'

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "Home",
        component: Home
    },
    {
        path: "/test",
        name: "test",
        component: test
    },
    {
        path: "/about",
        name: "about",
        component: about
    },
];

const router = new VueRouter({
    // mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;